import store from "../store/index";
import { Toast } from "vant";
import { getAddress } from "./public";
const axios = require("axios");

if (process.env.NODE_ENV === "development") {
  axios.defaults.baseURL = "/apii";
} else if (process.env.NODE_ENV === "production") {
  axios.defaults.baseURL = window.gurl.SERVICE_CONTEXT_PATH;
}
axios.defaults.withCredentials = true;
// axios.defaults.headers["Content-Type"] = "application/x-www-form-urlencoded";
axios.defaults.headers["Content-Type"] = "application/json";
axios.defaults.headers["Authorization"] = "Bearer " + store.getters.token;
const get = (url, data = {}) => {
  return new Promise((resolve, reject) => {
    axios
      .get(url, { params: data })
      .then((data) => {
        resolve(data);
      })
      .catch((err) => {
        reject(err);
      });
  });
};
const put = (url, data = {}) => {
  return new Promise((resolve, reject) => {
    axios
      .put(url, data)
      .then((data) => {
        resolve(data);
      })
      .catch((err) => {
        reject(err);
      });
  });
};
const post = (url, data = {}, formdata = true, configs = {}) => {
  if (formdata) {
    const formdata = new FormData();
    for (let k in data) {
      formdata.append(k, data[k]);
    }
    data = formdata;
  }
  return new Promise((resolve, reject) => {
    axios
      .post(url, data, configs)
      .then((data) => {
        resolve(data);
      })
      .catch((err) => {
        reject(err);
      });
  });
};
axios.interceptors.request.use((config) => {
  let { token } = store.getters;
  if (token) {
    config.headers.token = token;
  }
  return config;
});
axios.interceptors.response.use(
  (response) => {
    console.log(response);
    let { msg, code } = response.data;
    Toast.clear();
    if (code === -100) {
      getAddress();
    } else if (code != 200 && msg) {
      Toast(msg);
    }
    return response.data;
  },
  (err) => {
    console.log(err);
  }
);

const setDefaultToken = (token) => {
  axios.defaults.headers["Authorization"] = "Bearer " + token;
};

export { post, get, put, setDefaultToken };
