<template>
  <router-view />
</template>

<script>
import store from './store'
import { getAddress } from './utils/public';
export default {
  name: "App",
  created() {
    setTimeout(() => {
      let { wallet_address } = store.getters;
      if (!wallet_address) {
        getAddress();
      }
    }, 1000)
  }
}
</script>
<style lang="scss">
#app {
  height: 100%;
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  font-size: 12px;
}
</style>
