import { createStore } from "vuex";
// import storage from "./storage"; //Session
import storageL from "./storageLocal";
export default createStore({
  state: {
    token: storageL.get("token") || "",
    vipId: storageL.get("vipId") || "",
    wallet_address: storageL.get("wallet_address") || "",
    history_address: storageL.get("history_address") || "",
  },
  getters: {
    wallet_address: (state) => {
      return state.wallet_address;
    },
    history_address: (state) => {
      return state.history_address;
    },
    token: (state) => {
      return state.token;
    },
    vipId: (state) => {
      return state.vipId;
    },
  },
  mutations: {
    updateWalletAddress: (state, payload) => {
      state.wallet_address = payload;
      storageL.set("wallet_address", payload);
    },
    updateHistoryAddress: (state, payload) => {
      state.history_address = payload;
      storageL.set("history_address", payload);
    },
    updateToken: (state, payload) => {
      state.token = payload;
      storageL.set("token", payload);
    },
    updateVipId: (state, payload) => {
      state.vipid = payload;
      storageL.set("vipid", payload);
    },
  },
  actions: {},
  modules: {},
});
