<template>
  <div class="register-layer">
    <div class="main-title">官方推荐【去中心化】钱包</div>
    <div class="prompt">手机中有以下任一钱包即可跟随教程进入游戏，如尚未下载任何钱包，可点选任一钱包官网先进入下载并注册您的数字货币钱包。</div>
    <div class="table-cont">
      <div class="tr-list head">
        <div class="td-item center">钱包</div>
        <div class="td-item center">官网</div>
        <div class="td-item center">直达</div>
      </div>
      <div class="tr-list" v-for="(item, index) in datalist" :key="index">
        <div class="td-item">
          <img :src="item.image" />{{ item.title }}
        </div>
        <div class="td-item center underline">
          <a :href="item.href" target="_blank">{{ item.label }}</a>
        </div>
        <div class="td-item center">
          <a class="btn" :href="item.link" target="_blank">{{ item.value }}</a>
        </div>
      </div>
      <warn @open="handleOpen" />
    </div>
  </div>
</template>

<script>
import { walletList } from "../utils/resources"
import warn from "./Warn"
export default {
  name: 'Register',
  components: { warn },
  data() {
    return {
      datalist: walletList
    }
  },
  methods: {
    handleOpen(e) {
      if (e === 1) return;
      this.$emit("open", e);
    }
  }
}
</script>

<style lang="scss" scoped>
.register-layer {
  padding: 10px;

  .main-title {
    color: var(--main-color);
    font-size: 18px;
    font-weight: bold;
    text-align: center;
  }
  .prompt {
    padding-top: 10px;
    text-align: center;
  }
  .table-cont {
    .tr-list {
      display: flex;
      align-items: center;
      justify-content: center;
      .td-item {
        flex: 1;
        height: 40px;
        display: flex;
        align-items: center;
        img {
          width: 30px;
          height: 30px;
          margin-right: 5px;
        }
        a {
          color: var(--main-color);
          text-decoration: none;
        }
        .btn {
          width: 100%;
          height: 30px;
          line-height: 30px;
          border-radius: 30px;
          text-align: center;
          display: inline-block;
          background-color: #e5eeff;
        }
        &:nth-child(2) {
          flex: 2;
        }
      }

      .underline {
        a {
          text-decoration: underline;
        }
      }
      .center {
        justify-content: center;
      }
    }
    .head {
      font-size: 14px;
    }
  }
}
</style>