<template>
  <Header />
  <div class="container main-content">
    <div class="banner-cont">
      <img src="../assets/images/banner01.jpg" />
    </div>
    <div class="award-cont">
      <van-row class="cus-head">
        <van-col span="6">
          <span>千万USDT</span>
          <p>等你来赢</p>
        </van-col>
        <van-col span="6">
          <span>3s</span>
          <p>自动派奖</p>
        </van-col>
        <van-col span="6">
          <span>信誉实力</span>
          <p>品牌保证</p>
        </van-col>
        <van-col span="6">
          <span>7*24小时</span>
          <p>客服在线服务</p>
        </van-col>

      </van-row>
      <van-row gutter="8">
        <van-col span="18">
          <van-swipe class="my-swipe" :autoplay="0" indicator-color="blue">
            <van-swipe-item>
              <div class="banlance-cont">
                <div class="prize-pool">
                  <img src="../assets/images/jb.png" />
                  <div class="account-name">奖池实时余额</div>
                  <div class="link">波场查询
                    <van-icon name="arrow" />
                  </div>
                </div>
                <div class="amount">
                  <span>{{ awardBalance }}</span>
                  <span class="symbol">USDT</span>
                </div>
              </div>
            </van-swipe-item>
          </van-swipe>
        </van-col>
        <van-col class="link-cont" span="6">
          <a :href="'https://tronscan.io/#/address/' + paicaiAddr" target="_blank">派彩地址
            <van-icon name="arrow" />
          </a>
          <!-- <a href="https://tronscan.io/#/token20/TQXXgxtEeFAfJrmBX73oJ6QHHdG41SU26q" target="_blank">合约地址
            <van-icon name="arrow" />
          </a> -->
        </van-col>
      </van-row>
    </div>
    <div class="game-cont">
      <img class="title" src="../assets/images/t1.png" />
      <div class="prompt" @click="showRegister = true">必须使用<span>【去中心化钱包】</span>转账，<span>点击查看</span>官方推荐钱包</div>
      <div class="game-list">
        <game-item :dataItem="item" v-for="(item, index) in gameList" :key="index" :to="'/game?id=' + index" />
      </div>
    </div>
    <div class="process-cont">
      <div class="subtitle">
        <img src="../assets/images/cylc.png" />
        <div class="text">参与流程</div>
      </div>
      <div class="prompt" @click="showDownload = true">若无法打开以下网站，可以使用官方VPN<span>【相关软件下载】</span></div>
      <div class="tab-list">
        <div class="tab-item" :class="{ 'active': processId === index }" v-for="(item, index) in processList" :key="index" @click="processId = index">
          <div class="pro-icon">
            <span>{{ index + 1 }}</span>
            <img src="../assets/images/stepBg2.png" v-if="processId === index" />
            <img src="../assets/images/stepBg1.png" v-else />
          </div>
          <span>{{ item.title }}</span>
        </div>
      </div>
      <table-list v-if="processId === 0" title="数字币【去中心化】钱包下载地址" :datalist="walletList" :head-title="['平台', '网址', '直达']" :type="1" @open="handleOpen" />
      <table-list v-else-if="processId === 1" title="购买虚拟币" :datalist="buyList" :head-title="['平台', '网址', '教程']" />
      <game-content v-else-if="processId === 2" :datalist="gameList" @open="handleOpen"></game-content>
      <div class="tutorial" v-else-if="processId === 3">
        <div class="subtitle">结果查询教程</div>
        <van-button type="primary" color="#4080ff" to="/tutorial?">tronscan.io 查询教程</van-button>
        <van-button type="primary" color="#4080ff" to="/tutorial?id=1">tokenview.com 查询教程</van-button>
        <van-button type="primary" color="#4080ff" to="/tutorial?id=2">钱包查询教程</van-button>
        <van-button type="primary" color="#4080ff" to="/tutorial?id=3">平台查询教程</van-button>
        <div class="link-cont">
          <a href="https://tronscan.io/#/" target="_blank">进入<span>tronscan.io</span>查询</a>
          <a href="https://tokenview.com/cn" target="_blank">进入<span>tokenview.com</span>查询</a>
        </div>
      </div>
      <game-content v-else-if="processId === 4" :datalist="gameList" :type="2"></game-content>
    </div>
    <div class="why-cont">
      <div class="subtitle">
        <img src="../assets/images/why.png" />
        <div class="text">为什么选择我们</div>
      </div>
      <choose-us></choose-us>
    </div>
    <div class="question-cont">
      <div class="subtitle">
        <img src="../assets/images/cjwt.png" />
        <div class="text">常见问题</div>
      </div>
      <div class="ques-list">
        <div class="ques-item" v-for="(item, index) in questionList" :key="index">
          <div class="title">Q{{ index + 1 }}:{{ item.title }}</div>
          <div class="describe">{{ item.content }}</div>
        </div>
      </div>
    </div>
  </div>
  <Footer />
  <van-popup v-model:show="showRegister" closeable position="bottom">
    <register @open="handleOpen" />
  </van-popup>
  <van-popup v-model:show="showDownload" closeable position="bottom">
    <download />
  </van-popup>
  <van-popup v-model:show="showProhibit" closeable position="bottom">
    <prohibit @open="handleOpen" />
  </van-popup>
</template>

<script>
import Header from "../components/Header"
import GameItem from "../components/GameItem"
import TableList from "../components/TableList"
import GameContent from "../components/GameContent"
import ChooseUs from "../components/ChooseUs"
import Register from "../components/Register";
import Download from "../components/Download";
import Prohibit from "../components/Prohibit"
import Footer from "../components/Footer"
import { walletList, buyList, questionList, gameList } from "../utils/resources"
export default {
  name: 'Home',
  components: { Header, GameItem, TableList, GameContent, ChooseUs, Register, Download, Prohibit, Footer },
  data() {
    return {
      active: 0,
      awardBalance: '', //奖池余额
      largeBg: require("../assets/images/xyhx.jpg"),
      smallBg: require("../assets/images/dan.jpg"),
      processId: 0,
      processList: [
        { title: "注册钱包" }, { title: "购买虚拟币" },
        { title: "参与游戏" }, { title: "中奖查询" }, { title: "中奖回款" }
      ],
      paicaiAddr: '',
      walletList: walletList,
      buyList: buyList,
      showRegister: false,
      showDownload: false,
      showProhibit: false,
      gameList: gameList,
      questionList: questionList
    }
  },
  created() {
    this.getGameList();
  },

  methods: {
    getGameList() {
      this.get("get_games").then(({ list, pay_address, pay_num }) => {
        this.gameList.forEach(it => {
          list.find(({ name, address, trx, usdt }) => {
            if (it.name === name) {
              it.address = address;
              it.trx = trx;
              it.usdt = usdt;
              return true;
            }
            return false;
          })
        })
        this.paicaiAddr = pay_address;
        let balance = pay_num.toFixed(2);
        if (pay_num > 50000) {
          balance = pay_num.toFixed(1);
        }
        this.awardBalance = balance;
      })
    },
    handleOpen(type) {
      if (type === 1) {
        this.showRegister = true;
        this.showProhibit = false;
      } else if (type === 2) {
        this.showProhibit = true;
        this.showRegister = false;
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.container {
  .banner-cont {
    font-size: 0;

    img {
      width: 100%;
      border-radius: 8px;
    }
  }

  .award-cont {
    display: flex;
    -webkit-box-pack: justify;
    justify-content: space-between;
    -webkit-box-align: center;
    align-items: center;
    box-sizing: border-box;
    padding: 10px;
    margin-top: 10px;
    background-color: #f3f7ff;
    width: 100%;
    height: 100%;
    flex-direction: column;
    border: 1px solid #f3f7ff;
    border-radius: 8px;

    .van-row {
      width: 100%;
    }

    .cus-head {
      margin: 8px 0;

      .van-col {
        color: #333333;
        font-size: 12px;
        text-align: center;

        span {
          font-weight: bold;
          color: var(--main-color);
        }

        p {
          margin-top: 4px;
        }
      }
    }

    .my-swipe {
      overflow: hidden;
      border-radius: 8px;
      background: url('../assets/images/bg.gif') no-repeat;
      background-size: 100% 100%;

      .banlance-cont {
        color: #fff;
        height: 70px;
        padding: 5px 10px;
        box-sizing: border-box;

        .prize-pool {
          display: flex;
          align-items: center;
          justify-content: space-between;

          img {
            width: 18px;
            height: 18px;
            margin-right: 8px;
          }

          .account-name {
            font-size: 14px;
            font-weight: bold;
          }

          .link {
          }
        }

        .amount {
          font-size: 30px;
          font-weight: bold;
          display: flex;
          margin-top: 10px;
          align-items: baseline;
          justify-content: center;

          .symbol {
            color: #f9c100;
            font-size: 18px;
            padding-left: 10px;
          }
        }
      }
    }

    .link-cont {
      :deep(.van-button) {
        height: 32px;
        margin-bottom: 6px;
        border-radius: 6px;
        padding: 0 5px;

        .van-button__content {
          color: #333;
          font-size: 12px;
        }

        &:last-child {
          margin-bottom: 0;
        }

        &:active {
          &:before {
            opacity: 0;
          }
        }
      }

      a {
        width: 100%;
        color: #333;
        line-height: 68px;
        text-align: center;
        margin-bottom: 6px;
        border-radius: 6px;
        display: inline-block;
        background-color: #e4edfe;
      }
    }
  }

  .game-cont {
    padding-top: 10px;

    .title {
      width: 100%;
    }

    .prompt {
      text-align: center;
      line-height: 28px;

      span {
        color: var(--main-color);
      }
    }

    .game-list {
      padding-top: 10px;
      column-count: 2;
      column-gap: 2%;

      :deep(.game-item) {
        &:first-child {
          margin-top: 0;
        }
      }
    }
  }

  .subtitle {
    position: relative;
    margin: 10px 0;

    img {
      width: 100%;
    }

    .text {
      color: #333;
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 16px;
      font-weight: bold;
    }
  }

  .tab-list {
    display: flex;
    margin-top: 10px;
    align-items: center;

    .tab-item {
      width: 20%;
      text-align: center;

      .pro-icon {
        cursor: pointer;
        position: relative;
        font-size: 0;

        > img {
          max-width: 56px;
        }

        span {
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 20px;
          color: #fff;
          font-weight: bold;
        }
      }

      &.active {
        color: var(--main-color);
      }
    }
  }

  .process-cont {
    .prompt {
      color: #ff4444;
      text-align: center;

      span {
        color: var(--main-color);
      }
    }

    .tutorial {
      padding: 10px;
      margin-top: 10px;
      border-radius: 8px;
      background-color: #ebf1fb;

      .subtitle {
        font-size: 16px;
        font-weight: bold;
        margin-top: 0;
        text-align: center;
      }

      .van-button {
        width: 100%;
        border-radius: 6px;
        margin-bottom: 10px;
      }

      .link-cont {
        display: flex;
        height: 30px;
        align-items: center;
        justify-content: space-between;

        a {
          color: var(--main-color);
          text-decoration: none;
        }
      }
    }
  }

  .question-cont {
    .ques-list {
      .ques-item {
        border-radius: 8px;
        overflow: hidden;
        box-sizing: border-box;
        background-color: #ebf1fb;
        padding: 10px;
        margin-top: 15px;

        .title {
          font-size: 12px;
          font-weight: bold;
          line-height: 20px;
          color: #ec808d;
        }
      }
    }
  }
}
</style>
