<template>
  <div class="game-item" v-if="dataItem" @click="pageJump">
    <img :src="require('../assets/images/' + dataItem.image)" />
    <!-- <img src="https://q.yb-visa.com/static/media/xyzx.3dfb851b.jpg" /> -->
    <div class="content">
      <div class="title"></div>
      <!-- {{ dataItem.name }} -->
      <div class="tran-limit">
        <div>转账限额:</div>
        <div>{{ dataItem.usdt }} <span>USDT</span></div>
        <div>{{ dataItem.trx }} <span>TRX</span></div>
      </div>
      <div class="play-intro" v-if="introduce" @click.stop="show = true">
        <van-icon name="warning" size="12" />玩法介绍
      </div>
      <div class="odds">赔率 {{ dataItem.power }}x</div>
    </div>
  </div>
  <van-popup v-model:show="show" position="bottom" closeable>
    <div class="cus-layer">
      <div class="main-title">{{ dataItem.name }}玩法介绍</div>
      <instructions :name="dataItem.name" />
    </div>
  </van-popup>
</template>

<script>
import Instructions from '../components/Instructions';
export default {
  name: 'GameItem',
  components: { Instructions },
  props: {
    dataItem: {
      type: Object,
      default() {
        return null
      }
    },
    to: String,
    introduce: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      show: false,
    }
  },
  methods: {
    pageJump() {
      if (!this.dataItem) return;
      if (!this.to) return;
      this.$router.push(this.to);
    }
  }
}
</script>

<style lang="scss" scoped>
.game-item {
  color: #fff;
  position: relative;
  font-size: 0;
  overflow: hidden;
  border-radius: 8px;
  margin-top: 10px;

  > img {
    width: 100%;
  }

  .content {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    font-size: 12px;
    padding: 10px 8px 4px;
    box-sizing: border-box;

    .title {
      font-size: 18px;
      font-weight: bold;
      display: inline-block;
      min-height: 20px;
    }

    .tran-limit {
      margin-top: 5px;
      color: #02b5af;
       font-weight: bold;
    }

    .play-intro {
      position: absolute;
      top: 3px;
      right: 3px;
      cursor: pointer;
      padding: 3px 6px;
      border-radius: 6px;
      background: rgba($color: #318a7b, $alpha: 0.6);

      .van-icon {
        padding-right: 5px;
      }
    }

    .odds {
      position: absolute;
      bottom: 0px;
      right: 0px;
      height: 20px;
      width: 84px;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #9d2929;
      border-radius: 6px 0px 0px;
      overflow: hidden;
      background-color: rgba(248, 251, 255, 0.8);
      font-size: 12px;
    }
  }
}

.cus-layer {
  position: relative;

  .main-title {
    color: var(--main-color);
    font-size: 18px;
    font-weight: bold;
    text-align: center;
    padding: 10px 0;
  }

  .rule-cont {
    max-height: 400px;
    overflow-y: auto;
    padding: 10px;
  }
}
</style>