import { Toast } from "vant";
import store from "../store/index";
import { post, setDefaultToken } from "./axios";

export function getAddress() {
  if (typeof window.tronWeb !== "undefined") {
    const address = window.tronWeb.defaultAddress.base58;
    //console.log(address)
    if (address) {
      store.commit("updateWalletAddress", address);
    } else {
      // Toast("您没有登录钱包,无法获取您的地址");
    }
  } else {
    // Notify({ type: 'primary', message: '未检测到钱包,请手动输入地址' })
  }
}
export function Subtr(arg1, arg2) {
  let r1, r2, m, n;
  let r1Arr = arg1.toString().split(".");
  r1 = r1Arr.length > 1 ? r1Arr[1].length : 0;
  let r2Arr = arg2.toString().split(".");
  r2 = r2Arr.length > 1 ? r2Arr[1].length : 0;
  m = Math.pow(10, Math.max(r1, r2));
  n = r1 >= r2 ? r1 : r2;
  return ((arg1 * m - arg2 * m) / m).toFixed(n);
}
export function Login(address) {
  return new Promise((resolve, reject) => {
    let {vipId} = store.getters;
    post("login", {
      username: address,vipid:vipId || ''
    }).then((res) => {
      console.log(res);
      let { token } = res.data;
      if (token) Toast("登录成功!");
      setDefaultToken(token);
      store.commit("updateWalletAddress", address);
      store.commit("updateToken", token);
      resolve();
    }).catch(() => {
      reject();
    });
  });
}
