<template>
  <div class="down-cont">
    <div class="title">{{ title }}</div>
    <div class="table-cont">
      <div class="tr-list head">
        <div class="td-item" v-for="(item, index) in headTitle" :key="index">{{ item }}</div>
      </div>
      <div class="tr-list" v-for="(item, index) in datalist" :key="index">
        <div class="td-item">
          <img :src="item.image" />{{ item.title }}
        </div>
        <div class="td-item center underline">
          <a :href="item.href" target="_blank">{{ item.label }}</a>
        </div>
        <div class="td-item center">
          <a :href="item.link" target="_blank">{{ item.value }}</a>
        </div>
      </div>
      <warn v-if="type === 1" @open="handleOpen" />
    </div>
  </div>
</template>

<script>
import Warn from "./Warn";

export default {
  name: "TableList",
  props: {
    title: String,
    headTitle: {
      type: Array,
      default() {
        return [];
      }
    },
    datalist: {
      type: Array,
      default() {
        return [];
      }
    },
    type: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {};
  },
  components: { Warn },
  methods: {
    handleOpen(e) {
      this.$emit("open", e)
    }
  }
}
</script>

<style lang="scss" scoped>
.down-cont {
  border-radius: 8px;
  overflow: hidden;
  box-sizing: border-box;
  padding: 10px;
  margin-top: 15px;
  background-color: #ebf1fb;

  .title {
    font-size: 14px;
    font-weight: bold;
    text-align: center;
  }

  .table-cont {
    margin-top: 10px;
    background-color: #ebf1fb;

    .tr-list {
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: center;

      .td-item {
        flex: 1;

        > img {
          width: 24px;
          margin-left: 10px;
          margin-right: 5px;
          display: inline-block;
          vertical-align: middle;
        }

        a {
          color: var(--main-color);
          text-decoration: none;
        }

        &.center {
          text-align: center;
        }
      }

      .underline {
        a {
          text-decoration: underline;
        }
      }

      &:nth-child(even) {
        background-color: #d6ddec;
      }

      &:nth-child(odd) {
        background-color: #f3f7ff;
      }

      &:last-child {
        border-radius: 0 0 8px 8px;
      }

      &.head {
        color: #fff;
        text-align: center;
        border-radius: 8px 8px 0 0;
        background-color: var(--main-color);
      }
    }
  }
}
</style>