<!--  -->
<template>
  <van-tabs v-model:active="active" :line-height="0">
    <van-tab :title="item.name" v-for="(item, index) in datalist" :key="index">
      <template v-if="type === 1">
        <game-item :dataItem="item" :introduce="false" />
        <warn @open="handleOpen" />
        <div class="platform">
          <div class="address">平台官方下注地址<span>TRC20</span></div>
          <div class="qr-code">
            <img :src="require('../assets/images/' + item.address + '.png')" />
          </div>
          <div class="tran-addr" @click="copy(item.address)">
            <div class="wallet">{{ item.address }}</div>
            <div class="btn">点击复制</div>
          </div>
        </div>
      </template>
      <div class="how-play" v-else>
        <div class="subtitle">{{ item.name }}玩法</div>
        <div class="cus-list">
          <div class="cus-item" v-for="(str,idx) in item.instruce[1].list" :key="idx">
            <span>{{idx+1}}、</span>
            <div v-html="str"></div>
          </div>
          <div class="cus-item">
            <span>2、</span>中奖后系统自动返还转账金额1.95倍的同类数字币。(由于区块链技术为了保证资金安全，每笔交易都需记录，防篡改，且全球使用数字币人口庞大，转账后可能会出现些许延迟到账情况，敬请耐心等待)
          </div>
        </div>
      </div>
    </van-tab>
  </van-tabs>
</template>

<script>
import GameItem from "./GameItem"
import Warn from "./Warn"
import useClipboard from "vue-clipboard3";
export default {
  name: 'GameCotent',
  components: { GameItem, Warn },
  props: {
    type: {
      type: Number,
      default: 1
    },
    datalist: {
      type: Array,
      default() {
        return []
      }
    }
  },
  data() {
    return {
      active: 0,
      toClipboard: null,
    }
  },
  created() {
    const { toClipboard } = useClipboard();
    this.toClipboard = toClipboard;
  },
  methods: {
    copy(text) {
      this.toClipboard(text)
      this.$toast("复制成功!");
      console.log('Copied to clipboard')
    },
    handleOpen(e) {
      this.$emit("open", e)
    }
  }
}
</script>
<style lang='scss' scoped>
.van-tabs {
  margin-top: 10px;
  border-radius: 8px;
  overflow: hidden;
  background-color: #ebf1fb;

  :deep(.van-tabs__wrap) {
    .van-tabs__nav {
      background: #d9e6ff;

      .van-tab {
        font-size: 12px;
      }

      .van-tab--active {
        color: #0648b4;
        border-radius: 8px 8px 0 0;
        background-color: #ebf1fb;
      }
    }
  }

  :deep(.van-tabs__content) {
    padding: 0 10px 10px;

    .game-item {
      .odds {
        top: 0;
        bottom: initial;
        border-radius: 6px;
      }
    }
  }
}

.platform {
  text-align: center;

  .address {
    span {
      color: #ff4444;
      font-weight: bold;
    }
  }

  .qr-code {
    padding: 10px 0;

    img {
      width: 50%;
    }
  }

  .tran-addr {
    border-radius: 6px;
    border: 1px solid var(--main-color);
    background: var(--main-color);

    .wallet {
      padding: 8px 0;
      border-radius: 6px 6px 0 0;
      background-color: #fff;
    }

    .btn {
      color: #fff;
      padding: 7px 0;
    }
  }
}

.how-play {
  padding-top: 10px;

  .subtitle {
    font-weight: bold;
  }

  .cus-list {
    .cus-item {
      margin-top: 10px;
      display: flex;
    }
  }
}
</style>