<template>
  <div class="rule-cont">
    <div class="cus-list" v-if="gameData">
      <div class="rule-item" v-for="(item,index) in gameData.instruce" :key="index">
        <div class="subtitle">
          <img :src="item.image" />
          <span class="text">{{item.title}}</span>
        </div>
        <div class="content" v-if="item.type === 1">
          <div class="child" v-for="(it,idx) in item.list" :key="idx">
            <label>{{idx >= 9 ? '' : '0'}}{{idx + 1}}、</label>
            <div v-html="it"></div>
          </div>
        </div>
        <div class="area-cont" v-else>
          <div class="child" v-for="(list,idx) in item.list" :key="idx">
            <div v-for="(tt,i) in list" :key="i" v-html="tt"></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { gameList } from '../utils/resources'
export default {
  name: 'Instructions',
  props: {
    name: String
  },
  data() {
    return {
      gameData: null
    }
  },
  created() {
    this.gameData = gameList.find(it => it.name === this.name)
  },
  watch: {
    name(newVal) {
      this.gameData = gameList.find(it => it.name === newVal)
    }
  }
}
</script>

<style lang="scss" scoped>
.rule-cont {
  .cus-list {
    .rule-item {
      color: #7f7f7f;
      border-radius: 8px;
      overflow: hidden;
      box-sizing: border-box;
      background-color: #ffffff;
      padding: 10px;
      margin: 15px 0px;
      .subtitle {
        position: relative;
        img {
          width: 100%;
        }
        .text {
          position: absolute;
          left: 0px;
          bottom: 0px;
          font-size: 16px;
          font-weight: bold;
          color: rgb(51, 51, 51);
        }
      }
      .content {
        margin-top: 10px;
        .child {
          display: flex;
          line-height: 20px;
          :deep(span) {
            color: #d9001b;
          }
        }
      }
      .area-cont {
        .child {
          border-radius: 8px;
          overflow: hidden;
          display: flex;
          flex-direction: column;
          margin-top: 10px;
          box-sizing: border-box;
          background-color: #f3f7ff;
          padding: 10px;
          div {
            text-align: center;
            line-height: 20px;
            :deep(span) {
              color: #d9001b;
            }
          }
        }
      }
    }
  }
}
</style>