<template>
  <van-tabbar v-model="active" :before-change="beforeChange">
    <van-tabbar-item icon="home-o" to="/">首页</van-tabbar-item>
    <van-tabbar-item icon="point-gift" to="/welfare">福利</van-tabbar-item>
    <van-tabbar-item icon="service">客服</van-tabbar-item>
    <van-tabbar-item icon="manager" to="/mine">我的</van-tabbar-item>
  </van-tabbar>
  <van-popup v-model:show="openLayer" closeable round>
    <service />
  </van-popup>
</template>

<script>
import Service from "../components/Service";
export default {
  name: 'Footer',
  components: { Service },
  props: {
    index: Number,
  },
  data() {
    return {
      active: 0,
      openLayer: false
    }
  },
  created() {
    if (this.index) this.active = this.index;
  },
  methods: {
    beforeChange(index) {
      if (index === 2) {
        this.openLayer = true;
        return false;
      }
      return true;
    }
  }
}
</script>

