import { createRouter, createWebHashHistory } from "vue-router";
import Home from "../views/Home.vue";
import { getAddress } from "../utils/public";
import store from "../store";

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
    meta: { title: "首页", needLogin: false },
  },
  {
    path: "/about",
    name: "About",
    component: () => import("../views/About.vue"),
  },
  {
    path: "/tutorial",
    name: "Tutorial",
    component: () => import("../views/Tutorial.vue"),
    meta: { title: "", needLogin: false },
  },
  {
    path: "/welfare",
    name: "Welfare",
    component: () => import("../views/Welfare.vue"),
    meta: { title: "福利", needLogin: false },
  },
  {
    path: "/mine",
    name: "Mine",
    component: () => import("../views/Mine.vue"),
    meta: { title: "我的", needLogin: false },
  },
  {
    path: "/recordList",
    name: "RecoedList",
    component: () => import("../views/RecordList.vue"),
    meta: { title: "投注记录", needLogin: true },
  },
  {
    path: "/login",
    name: "Login",
    component: () => import("../views/Login.vue"),
    meta: { title: "登录", needLogin: false },
  },
  {
    path: "/game",
    name: "Recreation",
    component: () => import("../views/Recreation.vue"),
    meta: { title: "游戏", needLogin: false },
  },
  {
    path: "/index", name: "Index",
    component: () => import("../views/Index.vue"),
    meta: { title: "个人中心", needLogin: false },
  },
  {
    path: "/benefits", name: "Benefits",
    component: () => import("../views/Benefits.vue"),
    meta: { title: "个人福利", needLogin: false },
  },
];

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes,
});

router.beforeEach((to, from, next) => {
  let { needLogin } = to.meta;
  // 从vuex获取登录状态
  let { token } = store.getters;
  // document.title = title;
  let { vipid } = to.query;
  if (vipid) store.commit("updateVipId", vipid);
  if (needLogin && !token) {
    getAddress();
  } else {
    next();
  }
});
router.afterEach(() => {
  window.scrollTo(0, 0);
});

export default router;
